<template>
  <div id="c-ars">
    <!-- <van-button
      size="large"
      color="linear-gradient(to right, #4bb0ff, #6149f6)"
      @touchstart.prevent="startRecorder"
      @touchend="endRecorder"
      @mousedown.prevent="startRecorder"
      @mouseup.prevent="endRecorder"
    >点击说话</van-button>-->
    <div class="info">
      <p>请确保已打开显示端。</p>
      <p>您可以和小助手闲聊，也可以命令它播报行程、播报天气、讲笑话、跳舞等，更多技能等您发掘哦~</p>
    </div>
    <button
      id="microphone"
      @touchstart.prevent="startRecorder"
      @touchend="endRecorder"
      @mousedown.prevent="startRecorder"
      @mouseup.prevent="endRecorder"
    >点这个这个</button>
    <!-- 波形绘制区域 -->
    <!-- <div class="pd recpower">
      <div style="height:40px;width:300px;background:#999;position:relative;">
        <div class="recpowerx" style="height:40px;background:#0B1;position:absolute;"></div>
        <div class="recpowert" style="padding-left:50px; line-height:40px; position: relative;"></div>
      </div>
    </div>
    <div class="pd waveBox">
      <div
        style="height:100px;width:300px;border:1px solid #ccc;box-sizing: border-box;display:inline-block"
        class="recwave"
      ></div>
    </div>
    <button onclick="recPlay()">播放</button>-->
  </div>
</template>

<script>
import Recorder from 'recorder-core'
import 'recorder-core/src/engine/wav'
import 'recorder-core/recorder.wav.min.js'
import 'recorder-core/src/extensions/waveview.js'

export default {
  data () {
    return {
      userId: 1,
      client_id: 'lOQ5dD9hdxO4fYSpZC5ag9k7',
      client_secret: 'cEfW9QHZsnD0wivPHqwU6FDELSG4WjUu',
      baiduToken: null,
      rec: null,
      // wave: null,
      recBlob: null,
      dialogInt: null,
      websock: null,
      wsuri: 'ws://121.40.165.18:8800'
    }
  },
  mounted () {
    this.getBaiduToken();
    // this.initWebSocket(); //初始化websocket
    this.recOpen(); //初始化录音
  },
  destroyed () {
    this.recClose(); //关闭录音
    // this.websock.close() //离开路由之后断开websocket连接
  },
  methods: {
    //   获得百度的Token
    getBaiduToken: async function () {
      const { data: res } = await this.$http.post(`/getBaidu/oauth/2.0/token?grant_type=client_credentials&client_id=${this.client_id}&client_secret=${this.client_secret}`)
      this.baiduToken = res.access_token;
    },
    upload: function () {
      var self = this;
      var reader = new FileReader();
      reader.onloadend = async function () {
        const { data: res } = await self.$http.post('/vop.baidu.com/pro_api',
          {
            "format": "wav",
            "rate": 16000,
            "dev_pid": 80001,
            "channel": 1,
            "token": self.baiduToken,
            "cuid": "baidu_workshop",
            "len": self.recBlob.size,
            "speech": (/.+;\s*base64\s*,\s*(.+)$/i.exec(reader.result) || [])[1], // xxx为 base64（FILE_CONTENT）
          })
        window.console.log(res);
        if (res.err_no == '0') {
          self.websocketsend(res.result);
        } else {
          self.$toast.fail('没听清，麻烦再说一次呢~')
          self.websocketsend('[cant Recognition]');
        }
      };
      reader.readAsDataURL(this.recBlob);





    },
    startRecorder: function () {
      this.recStart();
    },
    endRecorder: function () {
      this.recStop();
    },

    /**调用open打开录音请求好录音权限**/
    recOpen: function () {//一般在显示出录音按钮或相关的录音界面时进行此方法调用，后面用户点击开始录音时就能畅通无阻了
      this.rec = null;
      // this.wave = null;
      this.recBlob = null;
      var self = this;
      var newRec = Recorder({
        type: "wav", sampleRate: 16000, bitRate: 16 //mp3格式，指定采样率hz、比特率kbps，其他参数使用默认配置；注意：是数字的参数必须提供数字，不要用字符串；需要使用的type类型，需提前把格式支持文件加载进来，比如使用wav格式需要提前加载wav.js编码引擎
        , onProcess: function (buffers, powerLevel, bufferDuration, bufferSampleRate) {
          //录音实时回调，大约1秒调用12次本回调
          document.querySelector(".recpowerx").style.width = powerLevel + "%";
          document.querySelector(".recpowert").innerText = bufferDuration + " / " + powerLevel;

          //可视化图形绘制
          self.wave.input(buffers[buffers.length - 1], powerLevel, bufferSampleRate);
        }
      });
      this.createDelayDialog(); //我们可以选择性的弹一个对话框：为了防止移动端浏览器存在第三种情况：用户忽略，并且（或者国产系统UC系）浏览器没有任何回调，此处demo省略了弹窗的代码
      newRec.open(function () {//打开麦克风授权获得相关资源
        self.dialogCancel(); //如果开启了弹框，此处需要取消

        self.rec = newRec;

        //此处创建这些音频可视化图形绘制浏览器支持妥妥的
        self.wave = Recorder.FrequencyHistogramView({ elem: ".recwave" });

        self.reclog("已打开录音，可以点击录制开始录音了", 2);
      }, function (msg, isUserNotAllow) {//用户拒绝未授权或不支持
        self.dialogCancel(); //如果开启了弹框，此处需要取消
        self.reclog((isUserNotAllow ? "UserNotAllow，" : "") + "打开录音失败：" + msg, 1);
      });

      window.waitDialogClick = function () {
        self.dialogCancel();
        self.reclog("打开失败：权限请求被忽略，<span style='color:#f00'>用户主动点击的弹窗</span>", 1);
      };
    },

    /**关闭录音，释放资源**/
    recClose: function () {
      if (this.rec) {
        this.rec.close();
        this.reclog("已关闭");
      } else {
        this.reclog("未打开录音", 1);
      }
    },

    /**开始录音**/
    recStart: function () {//打开了录音后才能进行start、stop调用
      if (this.rec && Recorder.IsOpen()) {
        this.recBlob = null;
        this.rec.start();
        this.reclog("已开始录音...");
      } else {
        this.reclog("未打开录音", 1);
      }
    },

    /**结束录音，得到音频文件**/
    recStop: function () {
      if (!(this.rec && Recorder.IsOpen())) {
        this.reclog("未打开录音", 1);
        return;
      }
      var self = this;
      this.rec.stop(function (blob, duration) {
        window.console.log(blob, (window.URL || window.webkitURL).createObjectURL(blob), "时长:" + duration + "ms");

        self.recBlob = blob;

        // 上传
        self.upload();



        self.reclog("已录制wav：" + duration + "ms " + blob.size + "字节，可以点击播放、上传了", 2);
      }, function (msg) {
        self.reclog("录音失败:" + msg, 1);
      });
    },
    /**播放**/
    recPlay: function () {
      if (!this.recBlob) {
        this.reclog("请先录音，然后停止后再播放", 1);
        return;
      }
      var cls = ("a" + Math.random()).replace(".", "");
      this.reclog('播放中: <span class="' + cls + '"></span>');
      var audio = document.createElement("audio");
      audio.controls = true;
      document.querySelector("." + cls).appendChild(audio);
      //简单利用URL生成播放地址，注意不用了时需要revokeObjectURL，否则霸占内存
      audio.src = (window.URL || window.webkitURL).createObjectURL(this.recBlob);
      audio.play();

      setTimeout(function () {
        (window.URL || window.webkitURL).revokeObjectURL(audio.src);
      }, 5000);
    },





    //recOpen我们可以选择性的弹一个对话框：为了防止移动端浏览器存在第三种情况：用户忽略，并且（或者国产系统UC系）浏览器没有任何回调
    showDialog: function () {
      if (!/mobile/i.test(navigator.userAgent)) {
        return;//只在移动端开启没有权限请求的检测
      }
      this.dialogCancel();

      //显示弹框，应该使用自己的弹框方式
      var div = document.createElement("div");
      document.body.appendChild(div);
      div.innerHTML = (''
        + '<div class="waitDialog" style="z-index:99999;width:100%;height:100%;top:0;left:0;position:fixed;background:rgba(0,0,0,0.3);">'
        + '<div style="display:flex;height:100%;align-items:center;">'
        + '<div style="flex:1;"></div>'
        + '<div style="width:240px;background:#fff;padding:15px 20px;border-radius: 10px;">'
        + '<div style="padding-bottom:10px;">录音功能需要麦克风权限，请允许；如果未看到任何请求，请点击忽略~</div>'
        + '<div style="text-align:center;"><a onclick="waitDialogClick()" style="color:#0B1">忽略</a></div>'
        + '</div>'
        + '<div style="flex:1;"></div>'
        + '</div>'
        + '</div>');
    },
    createDelayDialog: function () {
      var self = this;
      this.dialogInt = setTimeout(function () {//定时8秒后打开弹窗，用于监测浏览器没有发起权限请求的情况，在open前放置定时器利于收到了回调能及时取消（不管open是同步还是异步回调的）
        self.showDialog();
      }, 8000);
    },
    dialogCancel: function () {
      clearTimeout(this.dialogInt);

      //关闭弹框，应该使用自己的弹框方式
      var elems = document.querySelectorAll(".waitDialog");
      for (var i = 0; i < elems.length; i++) {
        elems[i].parentNode.removeChild(elems[i]);
      }
    },
    //recOpen弹框End

    reclog: function (s, color) {
      var now = new Date();
      var t = ("0" + now.getHours()).substr(-2)
        + ":" + ("0" + now.getMinutes()).substr(-2)
        + ":" + ("0" + now.getSeconds()).substr(-2);
      // var div = document.createElement("div");
      // var elem = document.querySelector(".reclog");
      // elem.insertBefore(div, elem.firstChild);
      // div.innerHTML = '<div style="color:' + (!color ? "" : color == 1 ? "red" : color == 2 ? "#0b1" : color) + '">[' + t + ']' + s + '</div>';
      window.console.log((!color ? "" : color == 1 ? "error" : color == 2 ? "" : color) + '[' + t + ']' + s);

    },




    // websocket
    initWebSocket () { //初始化weosocket
      const wsuri = this.wsuri;
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen () { //连接建立之后执行send方法发送数据
      let actions = { "test": "12345" };
      this.websocketsend(JSON.stringify(actions));
    },
    websocketonerror () {//连接建立失败重连
      this.initWebSocket();
    },
    websocketonmessage (e) { //数据接收
      // const redata = JSON.parse(e.data);
      window.console.log(e)
    },
    websocketsend (Data) {//数据发送
      this.websock.send(Data);
    },
    websocketclose (e) {  //关闭
      window.console.log('断开连接', e);
    }
  }
}
</script>

<style lang="less" scoped>
.info {
  width: 80%;
  margin: 20px auto;
  color: #555;
}
#microphone {
  background: #4bb0ff;
  color: #fff;
  border: 2px solid #ccc;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  display: block;
  transition: all 0.5s ease;
  &:hover,
  &:active {
    color: black;
  }
}
</style>