<template>
  <div>
    <van-form @submit="onSubmit">
      <!-- 选择行程日期 -->
      <van-calendar
        title="选择行程日期"
        :poppable="false"
        :show-confirm="false"
        :style="{ height: '300px' }"
        :row-height="35"
        @select="onDateConfirm"
      />
      <!-- 选择行程时间 -->
      <van-field
        readonly
        clickable
        name="datetimePicker"
        :value="form.time"
        label="时间选择"
        placeholder="点击选择时间"
        @click="showTimePicker = true"
      />
      <van-popup v-model="showTimePicker" position="bottom">
        <van-datetime-picker type="time" @confirm="onTimeConfirm" @cancel="showTimePicker = false" />
      </van-popup>

      <!-- 选择提醒日期时间 -->
      <van-field
        readonly
        clickable
        name="datetimePicker"
        :value="form.noticeTime"
        label="提醒时间"
        placeholder="点击选择提醒时间"
        @click="showDatetimePicker = true"
      />
      <van-popup v-model="showDatetimePicker" position="bottom">
        <van-datetime-picker
          v-model="happenTime"
          type="datetime"
          :min-date="minDate"
          :max-date="maxDate"
          @confirm="onDatetimeConfirm"
          @cancel="showDatetimePicker = false"
        />
      </van-popup>
      <!-- 填写提醒内容 -->
      <van-field
        v-model="form.content"
        name="内容"
        label="内容"
        :rules="[{ required: true, message: '请填写提醒内容' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>


<script>

export default {
  data () {
    return {
      value: '',
      showTimePicker: false,
      showDatetimePicker: false,
      form: {
        date: '',
        time: '',
        noticeTime: '',
        content: ''
      }
      ,
      minDate: new Date(2020, 1, 1),
      maxDate: new Date(2025, 10, 1),
      //   currentDate: '',
      show: false
    };
  },
  created () {
    var currentDate = new Date()
    this.form.date = currentDate.getFullYear() + '-' + (currentDate.getMonth() + 1) + '-' + currentDate.getDate()
    window.console.log(this.form.date)
  },
  computed: {
    happenTime: {
      get () {
        var date = this.form.date.split('-');
        date[1]--;
        var time = this.form.time.split(':');
        //   var date = new Date(this.date)
        window.console.log(new Date(...date, ...time));

        return new Date(...date, ...time)
      },
      set () { }
    }
  },
  methods: {
    onSubmit () {
      window.console.log('submit', this.form);
      //   TODO addNotice
      // const {data:res} = await this.$http.post('/my/notices/add',{  });
      // 成功通知
      this.$notify({ type: 'success', message: '添加成功' });
      setTimeout(() => {
        this.$router.go(-1)
      }, 1000);

    },
    onDateConfirm (date) {
      this.form.date = this.formatDate(date);
    },
    formatDate (date) {
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    },
    onTimeConfirm (time) {
      this.form.time = time;
      this.showTimePicker = false;
    },
    formatDateTime (date) {
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${date.getHours() < 10 ? '0' + date.getHours() : date.getHours()}:${date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()}`;
    },
    onDatetimeConfirm (time) {
      this.form.noticeTime = this.formatDateTime(time);
      this.showDatetimePicker = false;

    },
  },

};

</script>

<style lang="less" >
.van-form {
  .van-calendar__header-title {
    padding-top: 10px;
  }
  .van-calendar__header-subtitle,
  .van-calendar__header-title,
  .van-calendar__month-title {
    height: 30px;
    line-height: 20px;
  }
}
</style>